// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router/index.js";
import iView from "iview";
import "../my-theme/dist/iview.css";
import store from "./store/store";
import VueHtml5Editor from "vue-html5-editor";
Vue.config.productionTip = false;
const options = {
  // new VueHtml5Editor(options)
  // global component name
  name: "vue-html5-editor",
  // 
  // if set true,will append module name to toolbar after icon
  showModuleName: true,
  // classfont-awesome
  // custom icon class of built-in modules,default using font-awesome
  icons: {
    text: "ivu-icon ivu-icon-edit",
    color: "ivu-icon ivu-icon-android-color-palette",
    font: "ivu-icon ivu-icon-document-text",
    align: "ivu-icon ivu-icon-ios-shuffle",
    list: "ivu-icon ivu-icon-android-list",
    link: "ivu-icon ivu-icon-link",
    unlink: "ivu-icon ivu-icon-android-delete",
    tabulation: "ivu-icon ivu-icon-clipboard",
    image: "ivu-icon ivu-icon-image",
    hr: "ivu-icon ivu-icon-ios-loop",
    eraser: "ivu-icon ivu-icon-ios-close-outline",
    undo: "ivu-icon ivu-icon-refresh",
    "full-screen": "ivu-icon ivu-icon-monitor"
  },
  // 
  // config image module
  image: {
    //   max file size
    sizeLimit: 1024 * 1024,
    // ,base64
    // upload config,default null and convert image to base64
    upload: {
      url: null,
      headers: {},
      params: {},
      fieldName: {}
    },
    // ,localResizeIMG,null
    // compression config,default resize image by localResizeIMG (https://github.com/think2011/localResizeIMG)
    // set null to disable compression
    compress: {
      width: 1600,
      height: 1600,
      quality: 80
    },
    // ,
    // handle response datareturn image url
    uploadHandler(responseText) {


      //default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
      var json = JSON.parse(responseText);
      if (!json.ok) {
        alert(json.msg);
      } else {
        return json.data;
      }
    }
  },
  // en-uszh-cn
  //default en-us, en-us and zh-cn are built-in
  language: "zh-cn",
  // 
  i18n: {
    //specify your language here
    "zh-cn": {
      align: "",
      image: "",
      list: "",
      link: "",
      unlink: "",
      table: "",
      font: "",
      "full screen": "",
      text: "",
      eraser: "",
      color: "",
      "please enter a url": "",
      "create link": "",
      bold: "",
      italic: "",
      underline: "",
      "strike through": "",
      subscript: "",
      superscript: "",
      heading: "",
      "font name": "",
      "font size": "",
      "left justify": "",
      "center justify": "",
      "right justify": "",
      "ordered list": "",
      "unordered list": "",
      "fore color": "",
      "background color": "",
      "row count": "",
      "column count": "",
      save: "",
      upload: "",
      progress: "",
      unknown: "",
      "please wait": "",
      error: "",
      abort: "",
      reset: ""
    }
  },
  // 
  // the modules you don't want
  hiddenModules: [],
  // 
  // keep only the modules you want and customize the order.
  // can be used with hiddenModules together
  visibleModules: [
    "text",
    "color",
    "font",
    "align",
    "list",
    "link",
    "unlink",
    "tabulation",
    "image",
    "hr",
    "eraser",
    "undo",
    "full-screen"
  ],
  // examples
  // extended modules
  modules: {
    //omit,reference to source code of build-in modules
  }
};
Vue.use(iView);
Vue.use(VueHtml5Editor, options);
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  store,
  template: "<App/>",
  components: { App }
});



